import Authorisation from "./auth";

import React from "react";

import '../styles/auth-style.css';
import '../styles/header-styles.css';
import {Route, BrowserRouter, Switch, Link} from "react-router-dom";

class App extends React.Component {

    constructor(props){
        super(props);
        this.state = {isShow: false};
        this.showHidePopup = this.showHidePopup.bind(this);
    }

    showHidePopup(){
        let isShow = this.state.isShow;
        this.setState({isShow: !isShow});
    }

    render() {
        return (
            <div>
                <div>
                    <Link to='/sign_up'><li className="sign-in"><button onClick={this.showHidePopup}>Вход</button></li></Link>
                    {this.state.isShow ? <Authorisation /> : null}
                </div>
            </div>
        );
    }
}

export default App;